
.header{
    height: 55px;
    align-items: center;
    background-color: #124265;
    position: relative;
    display: flex;
    align-content: center;
}

.header1{
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.logo h3{
    font-weight: 700;
    color: rgb(231, 233, 241);
    margin-bottom: 0px;
}

.menu ul{
    display: flex;
    margin-bottom: 0px;
    list-style: none;
}

.menu ul li{
    align-self: center;
   margin: 0px 20px;
}

.menu ul li a{
    display: flex;
    justify-content: space-between;
    font-size: 15px;
    text-decoration: none;
    color: #fff;
    transition: 0.4s ease all;
    padding: 0px 5px;
 }

 .menu ul li a:hover{
   color: rgb(166, 181, 247);
 }

 .menu ul li:nth-child(8){
     border-radius: 3PX;
     padding: 8PX 20PX;
     background-color: #2487ce;
 }

 .menu ul li:nth-child(6){
     top: 0%;
     position: relative;
 }

 .menu ul li:nth-child(6):hover .sub-menu{
     display: block;
 }

 .menubtn{
     display: none;
 }

 .sub-menu{
     display: none;
     left: -100%;
     width: 250px;
     position: absolute;
 }

.sub-menu ul{
    border-radius: 3px;
    padding:  13px;
    box-shadow: 1px 1px 3px 2px grey;
    background-color: rgb(243, 243, 245);
    flex-direction: column;
    display: flex;
    margin-bottom: 0px;
    list-style: none;
}

.sub-menu ul li{
    margin: 6px 0px;
    padding: 8px 20px;
  padding-left: 0px;
    width: 100%;
    background-color: #124265;
    align-self: center;
}

.sub-menu ul li a{
    font-size: 15px;
    text-decoration: none;
    color: #fff;
    transition: 0.6s ease all
 }

 .sub-menu ul li a:hover{
   color: rgb(166, 181, 247);
 }

 .sub-menu ul li:nth-child(2):hover .sub-sub-menu{
    display: block;
}


 .sub-sub-menu{
     display: none;
     background-color: green;
    left: -100%;
    top: 25%;
    width: 250px;
    position: absolute;
}

.sub-sub-menu ul{
    border-radius: 3px;
    box-shadow: 1px 1px 3px 2px grey;
   padding:  13px;
   background-color: rgb(243, 243, 245);
   flex-direction: column;
   display: flex;
   margin-bottom: 0px;
   list-style: none;
}

.sub-sub-menu ul li{
   margin: 6px 0px;
   padding: 8px 20px;
 padding-left: 0px;
   width: 100%;
   /* background-color: red; */
   align-self: center;
}

.sub-sub-menu ul li a{
   font-size: 15px;
   text-decoration: none;
   color: #fff;
   transition: 0.6s ease all
}

.sub-sub-menu ul li a:hover{
  color: rgb(166, 181, 247);
}

.menubtn{
    color: white;
    font-size: 30px;
}

.menubtn i{
    cursor: pointer;
}

@media screen and (max-width:1250px) {
   .show{
       display: block;
   }

   .hide{
       display: none;
   }

    .menushow{
        position: absolute;
        left: 0%;
  /* display: block; */
   } 

   .menuhide{
    position: absolute;
    left: -100%;
   } 

    .menu{
        top: 100%;
        position: absolute;
        /* left: 0%; */
        width: 300px;
        min-height: 100vh;
        padding-left: 0px;
        background-color: #124265;
        overflow: scroll;
        transition: 0.6s ease all
    }
    .menu ul{
        padding-left: 10px;
        display: flex;
        flex-direction: column;
    }

    .menu ul li{
        margin-top: 8px;
        align-self: auto !important;
        padding: 10px 20px;
       align-items: flex-start;
    }

    .sub-menu{
        overflow: visible;
        display: none;
        left: 0%;
        width: 100%;
        position: absolute;
    }

    .sub-sub-menu{
        overflow: visible;
        display: none;
        left: 0%;
        width: 100%;
        position: absolute;
    }
}